@import "../../assets/styles/custom";

.mainMenu {
	padding: 0 !important;
	border-radius: 10px;
	overflow-y: auto;
	.item {
		// overflow: hidden;

		&:target {
			.subMenu {
				max-height: 10em;
			}
		}

		.btn {
			position: relative;
			padding: 0 20px;
			letter-spacing: 0.025em;
			color: $text-color-1;
		}
		.subMenu {
			a {
				position: relative;
				padding: 12px 20px 12px 36px;
				color: $text-color-1;
				&:before {
					content: "";
					opacity: 0;
					transition: opacity 0.25s;
				}
				&:after {
					content: "";
					opacity: 0;
					transition: opacity 0.25s;
				}
				&:last-child {
					border: none;
				}
			}
		}
		.accordionLink {
			border-radius: 12px;
			transition: all 0.3s;
			svg {
				transition: all 0.3s;
			}
			&:hover {
				color: $white-color;
				background: $primary-color-1;
				svg {
					color: $white-color;
				}
			}
			&.active {
				overflow: hidden;
				color: $white-color;
				background: linear-gradient(
					to bottom,
					$primary-color-2 0%,
					$primary-color-2 50%,
					$primary-color-1 51%,
					$primary-color-1 100%
				);
				border-bottom: 1px solid $primary-color-2;
				svg {
					color: $white-color;
				}
				&::before,
				&::after {
					content: "";
					position: absolute;
					top: 0;
					opacity: 1;
					width: 6px;
					height: 0;
					border-top: 24px solid transparent;
					border-bottom: 24px solid transparent;
				}
				&:before {
					left: 0;
					border-left: 11px solid $secondary-color-1;
				}
				&:after {
					right: 0;
					border-right: 11px solid $secondary-color-1;
				}
			}
		}
	}
}

