@import "../../assets/styles/custom";

.fc {
	overflow-y: auto;
	padding-top: 0.875rem;
	padding-left: 1rem;
	padding-right: 1rem;
	height: 100%;
	.fc-popover {
		z-index: 1050;
	}
	.fc-timegrid-slot-label {
		font-size: 0.875rem !important;
	}
	.fc-more-popover .fc-popover-body {
		max-height: 150px;
		overflow-y: auto;
	}
	.fc-popover-title {
		letter-spacing: 0.015em;
		font-size: 0.825rem;
	}
	.fc-highlight {
		background-color: rgb(255 196 196 / 30%);
	}
	.fc-list-empty {
		background-color: $white-color;
	}
	.fc-event-title-container {
		padding-left: 0.25rem;
		padding-right: 0.25rem;
	}
}
.custom_calendar {
	background-color: $white-color;
	border-radius: 0.5rem;
	overflow: hidden;
	.fc-timegrid-axis-frame a {
		font-weight: 700;
		color: white;
	}
	.fc-col-header-cell a {
		font-weight: 700;
		color: white;
	}
	.fc-scrollgrid-sync-inner {
		font-size: 0.875rem;
	}
	.fc-daygrid-day-top {
		justify-content: center;
	}
	.fc-daygrid-day-events {
		font-size: 1rem;
	}
	&.fc-dayGridMonth-view,
	&.fc-timeGridWeek-view,
	&.fc-dayGridWeek-view,
	&.fc-dayGridDay-view,
	&.fc-timeGridDay-view {
		td,
		th {
			padding: 2px 6px !important;
		}
	}
	&.fc-listMonth-view {
		td {
			font-size: 0.875rem !important;
			padding: 8px 12px !important;
		}
		.fc-list-event-graphic {
			padding: 8px 0 !important;
		}
	}
	&.fc-multiMonthYear-view {
		// padding-bottom: 8.5rem;
		table {
			thead tr th {
				padding: 8px 0 !important;
			}
			tbody tr td {
				padding: 0 !important;
			}
		}
	}
	tr {
		padding: 4px 8px;
		height: auto;
	}
	.fc-event-title {
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.fc-list-day-cushion {
		border-radius: 0.5rem;
	}
}

.punchlog_btngrpwrp {
	button {
		color: $secondary-color-1;
		border-color: $secondary-color-1;
		&:hover {
			border-color: $secondary-color-1;
		}
		&.primary {
			color: $white-color;
			background-color: $secondary-color-1;
		}
	}
}
.staffrosterCalendar {
	.fc .fc-daygrid-body-unbalanced .fc-daygrid-day-events {
		min-height: 0;
	}
}

@media (max-width: 575.98px) {
	.fc {
		padding-top: 0.25rem;
	}
	.custom_calendar.fc-listMonth-view {
		.fc-scroller {
			padding-bottom: 4rem;
		}
	}
	.custom_calendar {
		&.fc-dayGridMonth-view,
		&.fc-multiMonthYear-view {
			table {
				thead tr th,
				tbody tr td {
					div {
						font-size: 0.75rem !important;
					}
				}
			}
		}
		&.fc-dayGridMonth-view {
			td,
			th {
				padding: 2px 0 !important;
			}
		}
		&.fc-multiMonthYear-view {
			padding-bottom: 2.75rem;
		}
	}
}
